html {
  font-size: 100%;
  box-sizing: border-box;
}

*, *::before, *::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background: var(--bg);
  font-family: var(--font-regular);
  color: var(--text4);
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.1875rem;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

a, a:visited, a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --bg: hsl(230, 17%, 14%);
  --top-bg: hsl(225, 100%, 98%);
  --text1: hsl(228, 34%, 66%);
  --text2: hsl(228, 12%, 44%);
  --text3: hsl(230, 17%, 14%);
  --text4: hsl(0, 0%, 100%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --card: hsl(228, 28%, 20%) ;
}

.container {
  padding: 0 1.5625rem;
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

@font-face {
  font-family: "source_sans_proregular";
  src: url("fonts/sourcesanspro-regular-webfont.woff2") format("woff2");
}
@font-face {
  font-family: "source_sans_probold";
  src: url("fonts/sourcesanspro-bold-webfont.woff2") format("woff2");
}
:root {
  --font-regular: "source_sans_proregular";
  --font-bold: "source_sans_probold";
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
  color: var(--text1);
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3.75rem 1fr;
  grid-template-rows: 1fr 1.5rem;
  gap: 0.3rem 0.5rem;
  border: none;
  margin: 0;
}
.toggle label {
  font-family: var(--font-bold);
  font-size: 0.875rem;
  align-self: center;
}
.toggle label[for=dime] {
  grid-column: 1/2;
  justify-self: end;
}
.toggle label[for=quiet-mode] {
  grid-column: 1/4;
  grid-row: 2/3;
  justify-self: center;
  align-self: start;
}
.toggle label[for=money] {
  grid-column: 3/4;
}
.toggle__wrapper {
  position: relative;
  grid-column: 2/3;
  grid-row: 1/2;
  height: 1.5rem;
}
.toggle input[type=radio] {
  opacity: 0;
  margin: 0;
  width: 1.1875rem;
  height: 1.1875rem;
  margin-right: -0.125rem;
}
.toggle input[type=radio]:focus ~ .toggle__button {
  border: 2px solid whitesmoke;
}
.toggle__background {
  position: absolute;
  display: block;
  height: 100%;
  width: 100%;
  top: 0;
  border-radius: 0.75rem;
  overflow: hidden;
  background: linear-gradient(225deg, var(--toggle-bg-end) 0%, var(--toggle-bg-start) 98.02%);
  pointer-events: none;
}
.toggle__button {
  position: absolute;
  left: 0.1875rem;
  top: 0.1875rem;
  right: 100%;
  width: 1.125rem;
  height: 1.125rem;
  background-color: var(--bg);
  border-radius: 50%;
  transition: all 100ms ease-in;
}
.toggle #money:checked ~ .toggle__button {
  left: calc(100% - 21px);
  right: 3px;
}
.toggle #quiet-mode:checked ~ .toggle__button {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}

.card {
  background: var(--card);
  color: var(--text1);
  width: 15.9375rem;
  text-align: center;
  padding: 1.5625rem;
}
.card__title {
  color: var(--top-bg);
  font-family: var(--font-bold);
  font-weight: 700;
}